/* Modern API Testing Dashboard */
#api {
  min-height: 100vh;
  background: linear-gradient(135deg, #2c3e50 0%, #000000 100%);
  padding: 80px 20px 40px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.api-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

/* Header Section */
.api-header {
  text-align: center;
  margin-bottom: 40px;
  color: white;
}

.api-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  display: block;
}

.api-header h1 {
  font-size: 3rem;
  font-weight: 700;
  margin: 0 0 15px 0;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.api-header p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin: 0;
  font-weight: 300;
}

/* Cards Grid */
.api-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

/* Card Base Styles */
.api-card {
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.api-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.card-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 2px solid #f0f0f0;
}

.card-icon {
  font-size: 1.5rem;
}

.card-header h3 {
  font-size: 1.4rem;
  font-weight: 600;
  color: #2c3e50;
  margin: 0;
}

/* Endpoint Card */
.endpoint-card {
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

.endpoint-info {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.endpoint-url {
  background: #2c3e50;
  color: white;
  padding: 12px 16px;
  border-radius: 8px;
  font-family: 'Monaco', 'Menlo', monospace;
}

.endpoint-url code {
  font-size: 1rem;
  font-weight: 600;
}

.endpoint-full {
  display: flex;
  align-items: center;
  gap: 10px;
  background: #f8f9fa;
  padding: 12px;
  border-radius: 8px;
  font-size: 0.9rem;
  word-break: break-all;
}

.copy-btn {
  background: #2c3e50;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 6px 10px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.3s ease;
  min-width: 40px;
}

.copy-btn:hover {
  background: #000000;
  transform: scale(1.05);
}

.view-data-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, #4CAF50, #45a049);
  color: white;
  text-decoration: none;
  padding: 12px 20px;
  border-radius: 10px;
  font-weight: 600;
  transition: all 0.3s ease;
  text-align: center;
}

.view-data-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(76, 175, 80, 0.3);
  color: white;
}

/* Form Card */
.form-card {
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
}

.api-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: #2c3e50;
  font-size: 1rem;
}

.label-icon {
  font-size: 1.1rem;
}

.form-input,
.form-select {
  padding: 12px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
}

.form-input:focus,
.form-select:focus {
  outline: none;
  border-color: #2c3e50;
  box-shadow: 0 0 0 3px rgba(44, 62, 80, 0.1);
}

.form-input::placeholder {
  color: #a0aec0;
}

.submit-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background: linear-gradient(135deg, #2c3e50, #000000);
  color: white;
  border: none;
  padding: 15px 25px;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

/* Spinner Animation */
.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Response Card */
.response-card {
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
  border-left: 4px solid #2c3e50;
}

.response-content {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 16px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 1rem;
}

.status-indicator.success {
  background: linear-gradient(135deg, #d4edda, #c3e6cb);
  color: #155724;
}

.status-indicator.error {
  background: linear-gradient(135deg, #f8d7da, #f5c6cb);
  color: #721c24;
}

.response-details {
  background: #f8f9fa;
  border-radius: 10px;
  padding: 20px;
  border-left: 4px solid #28a745;
}

.detail-item {
  margin-bottom: 10px;
  font-size: 0.95rem;
}

.detail-item:last-child {
  margin-bottom: 0;
}

.error-details {
  background: #f8f9fa;
  border-radius: 10px;
  padding: 15px;
  border-left: 4px solid #dc3545;
  color: #721c24;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 0.9rem;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  #api {
    padding: 100px 15px 30px;
  }

  .api-header h1 {
    font-size: 2.5rem;
  }

  .api-header p {
    font-size: 1.1rem;
  }

  .api-cards {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .api-card {
    padding: 20px;
  }

  .endpoint-full {
    flex-direction: column;
    align-items: stretch;
    gap: 10px;
  }

  .copy-btn {
    align-self: flex-start;
  }
}

@media (max-width: 480px) {
  .api-header h1 {
    font-size: 2rem;
  }

  .api-icon {
    font-size: 3rem;
  }

  .api-card {
    padding: 15px;
  }

  .card-header h3 {
    font-size: 1.2rem;
  }
}
